import React, { useState, useMemo, useEffect } from "react";
import GetcodeButton from "../../../customComponent/GetcodeButton";
import AuthService from "../../../api/services/AuthService";
import LoaderHelper from "../../../customComponent/Loading/LoaderHelper";
import { alertErrorMessage, alertSuccessMessage } from "../../../customComponent/CustomAlertMessage";
import Web3Modal from "web3modal";
import WalletConnectProvider from "@walletconnect/web3-provider";
import { default as Web3 } from 'web3';
import { Link, useNavigate } from "react-router-dom";
import moment from "moment";
import HomePage from "../HomePage";

const WithdrawPage = () => {

    const loginid = localStorage.getItem("loginid");
    const [nsdtWithdrawalPrice, setNsdtWithdrawalPrice] = useState([]);
    const [walletAddr, setWalletAddr] = useState("");
    const [amount, setAmount] = useState("");
    const [walletType, setWalletType] = useState("BEP20");
    const [otp, setOtp] = useState("");
    const [transtionPass, setTranstionPass] = useState("");
    const [calculateAmount, setCalculateAmount] = useState([]);
    const [transtionHash, setTranstionHash] = useState('');
    const [admintxHash, setAdmintxHash] = useState('');
    const [withdrawalStatus, setWithdrawalStatus] = useState([]);
    const navigate = useNavigate();
    const [mainWallet, setMainWallet] = useState([]);

    const [lastWithdrawal, setLastWithdrawal] = useState([]);

    const [activeScreen, setActiveScreen] = useState('withdrawalPage');

    var web3

    const handleInputChange = (event) => {
        switch (event.target.name) {
            case "amount":
                setAmount(event.target.value);
                if (event.target.value === "") {
                    setCalculateAmount('');
                }
                break;
            case "walletType":
                setWalletType(event.target.value);
                break;
            case "otp":
                setOtp(event.target.value);
                break;
            case "transtionPass":
                setTranstionPass(event.target.value);
                break;
            default:
                break;
        }
    }

    const handleResetInput = () => {
        setAmount("");
        setOtp("");
        setTranstionPass("");
    }

    const handleWithdrowIncome = async (walletType, walletAddr, amount, otp, transtionPass, txHash, adtxHash) => {
        console.log(txHash, 'txHashsdf');
        LoaderHelper.loaderStatus(true);
        await AuthService.withdrawIncome(walletType, walletAddr, amount, otp, transtionPass, txHash, adtxHash).then(async result => {
            if (result?.success) {
                LoaderHelper.loaderStatus(false);
                try {
                    alertSuccessMessage(result.msg);
                    handleResetInput();
                    navigate("/dashboard");
                    window.location.reload(false);
                } catch (error) {
                    alertErrorMessage(error);
                }
            } else {
                LoaderHelper.loaderStatus(false);
                alertErrorMessage(result?.message);
            }
        });
    }

    const handleGetCode = async (loginid) => {
        LoaderHelper.loaderStatus(true);
        await AuthService.sendCode(loginid).then(async result => {
            if (result.message === "OTP Send Successfully") {
                LoaderHelper.loaderStatus(false);

                try {
                    alertSuccessMessage(result.message);
                } catch (error) {
                    //console.log('error', `${error}`);
                }
            } else {
                LoaderHelper.loaderStatus(false);

                alertErrorMessage(result.message);
            }
        });
    }

    // console.log(amount,'amount');

    const handleCalculateTbac = () => {
        setCalculateAmount(amount / nsdtWithdrawalPrice);
    }

    const providerOptions = {
        walletconnect: {
            package: WalletConnectProvider, // required
            options: {
                rpc: {
                    56: "https://bsc-dataseed.binance.org/",
                }
            }
        }
    }

    /*  useEffect(() => {
         // Update the document title using the browser API
         const accounts = web3.eth.getAccounts()
         setWalletAddr(accounts[0])
     }, [walletAddr]); */

    const web3Modal = new Web3Modal({
        network: "binance",
        cacheProvider: true,
        providerOptions
    })

    const WalletConnect = async () => {
        const provider = await web3Modal.connect();
        web3 = new Web3(provider);
        const accounts = await web3.eth.getAccounts()
        var account = accounts[0];
        setWalletAddr(accounts[0])
    }

    // Web 3 Code
    const token_abi = [{ "inputs": [], "stateMutability": "nonpayable", "type": "constructor" }, { "anonymous": false, "inputs": [{ "indexed": true, "internalType": "address", "name": "_owner", "type": "address" }, { "indexed": true, "internalType": "address", "name": "_spender", "type": "address" }, { "indexed": false, "internalType": "uint256", "name": "_value", "type": "uint256" }], "name": "Approval", "type": "event" }, { "anonymous": false, "inputs": [{ "indexed": true, "internalType": "address", "name": "_from", "type": "address" }, { "indexed": true, "internalType": "address", "name": "_to", "type": "address" }, { "indexed": false, "internalType": "uint256", "name": "_value", "type": "uint256" }], "name": "Transfer", "type": "event" }, { "inputs": [{ "internalType": "address", "name": "_owner", "type": "address" }, { "internalType": "address", "name": "_spender", "type": "address" }], "name": "allowance", "outputs": [{ "internalType": "uint256", "name": "remaining", "type": "uint256" }], "stateMutability": "view", "type": "function" }, { "inputs": [{ "internalType": "address", "name": "_spender", "type": "address" }, { "internalType": "uint256", "name": "_amount", "type": "uint256" }], "name": "approve", "outputs": [{ "internalType": "bool", "name": "success", "type": "bool" }], "stateMutability": "nonpayable", "type": "function" }, { "inputs": [{ "internalType": "address", "name": "_owner", "type": "address" }], "name": "balanceOf", "outputs": [{ "internalType": "uint256", "name": "balance", "type": "uint256" }], "stateMutability": "view", "type": "function" }, { "inputs": [{ "internalType": "uint256", "name": "amount", "type": "uint256" }], "name": "burn", "outputs": [], "stateMutability": "nonpayable", "type": "function" }, { "inputs": [{ "internalType": "address payable", "name": "_newOwner", "type": "address" }], "name": "changeOwnership", "outputs": [], "stateMutability": "nonpayable", "type": "function" }, { "inputs": [], "name": "decimals", "outputs": [{ "internalType": "uint8", "name": "", "type": "uint8" }], "stateMutability": "view", "type": "function" }, { "inputs": [{ "internalType": "uint256", "name": "amount", "type": "uint256" }], "name": "mint", "outputs": [], "stateMutability": "nonpayable", "type": "function" }, { "inputs": [], "name": "name", "outputs": [{ "internalType": "string", "name": "", "type": "string" }], "stateMutability": "view", "type": "function" }, { "inputs": [], "name": "owner", "outputs": [{ "internalType": "address", "name": "", "type": "address" }], "stateMutability": "view", "type": "function" }, { "inputs": [], "name": "symbol", "outputs": [{ "internalType": "string", "name": "", "type": "string" }], "stateMutability": "view", "type": "function" }, { "inputs": [], "name": "totalSupply", "outputs": [{ "internalType": "uint256", "name": "", "type": "uint256" }], "stateMutability": "view", "type": "function" }, { "inputs": [{ "internalType": "address", "name": "_to", "type": "address" }, { "internalType": "uint256", "name": "_amount", "type": "uint256" }], "name": "transfer", "outputs": [{ "internalType": "bool", "name": "success", "type": "bool" }], "stateMutability": "nonpayable", "type": "function" }, { "inputs": [{ "internalType": "address", "name": "_from", "type": "address" }, { "internalType": "address", "name": "_to", "type": "address" }, { "internalType": "uint256", "name": "_amount", "type": "uint256" }], "name": "transferFrom", "outputs": [{ "internalType": "bool", "name": "success", "type": "bool" }], "stateMutability": "nonpayable", "type": "function" }]

    const token_address = '0xA2D4d1e509ba872d1D3FF5d9Fa5a250c4B3c7c79';//Contract Address

    const numberToHex = (_num) => {
        var inputAmt = _num;
        var weiAmt = (100000000 * inputAmt);
        var hexaDecimal = "0x" + weiAmt.toString(16);
        return hexaDecimal;
    }

    let nsdtCoin = calculateAmount;
    let userNsdtCoin = parseFloat(nsdtCoin - (nsdtCoin * '15' / '100'))
    let adminNsdtCoin = parseFloat(nsdtCoin * '15' / '100')
    let hex = numberToHex(userNsdtCoin.toFixed(2));


    // console.log(calculateAmount, 'calculateAmount');

    const transferToAdmin = async () => {
        LoaderHelper.loaderStatus(true);
        try {
            // LoaderHelper.loaderStatus(false);

            const web3 = new Web3(new Web3.providers.HttpProvider('https://bsc-dataseed.binance.org/'))
            var contract = new web3.eth.Contract(token_abi, token_address)
            let value = hex

            // console.log(value, 'value');
            let privateKey = 'aad89e5dca7116afad4c3af245ff071eb37932752552f3598127679c3d9ac523';
            let to_address = walletAddr.toLowerCase();
            const account = web3.eth.accounts.privateKeyToAccount(privateKey).address;
            const transaction = contract.methods.transfer(to_address, value)
            const options = {
                'from': account,
                'to': contract.options.address,
                'data': transaction.encodeABI(),
                'gas': 200000,
            };
            const signed = await web3.eth.accounts.signTransaction(options, privateKey);
            const receipt = await web3.eth.sendSignedTransaction(signed.rawTransaction);
            setTranstionHash(receipt?.transactionHash)
            transferToUser(receipt?.transactionHash);
            // LoaderHelper.loaderStatus(false);
        } catch (error) {
            LoaderHelper.loaderStatus(false);
            console.log(error, 'error');
            alertErrorMessage('OOps Something is Wrong With Your Wallet');
        }
        // LoaderHelper.loaderStatus(false);


    };

    let userHex = numberToHex(adminNsdtCoin.toFixed(2));

    const transferToUser = async (txHash) => {
        LoaderHelper.loaderStatus(true);

        // Web3 instance
        const web3 = new Web3(new Web3.providers.HttpProvider('https://bsc-dataseed.binance.org/'))
        var contract = new web3.eth.Contract(token_abi, token_address)
        let value = userHex
        let privateKey = 'aad89e5dca7116afad4c3af245ff071eb37932752552f3598127679c3d9ac523';
        let adminDepositAddr = '0xAea99ef6883Bf422224912fdfC7B07d439DE6cf8'
        let to_address = adminDepositAddr.toLowerCase();
        const account = web3.eth.accounts.privateKeyToAccount(privateKey).address;
        const transaction = contract.methods.transfer(to_address, value)
        const options = {
            'from': account,
            'to': contract.options.address,
            'data': transaction.encodeABI(),
            'gas': 200000,
        };
        const signed = await web3.eth.accounts.signTransaction(options, privateKey);
        const receipt = await web3.eth.sendSignedTransaction(signed.rawTransaction);

        setAdmintxHash(receipt?.transactionHash);

        handleWithdrowIncome(walletType, walletAddr, amount, otp, transtionPass, txHash, receipt?.transactionHash)
        // console.log(receipt?.transactionHash, 'receipt');
    };



    useEffect(() => {
        handleUserBalance();
        handleNsdtAmount();
    }, [])


    const handleUserBalance = async () => {
        await AuthService.getUserBalance().then(async result => {
            if (result?.success) {
                try {
                    setWithdrawalStatus(result?.success?.withdrawalStatus);
                    setMainWallet(result?.success?.mainWallet);
                    setLastWithdrawal(result?.success?.lastWithdrawal)

                } catch (error) {
                    alertErrorMessage(error);
                }
            } else {
                alertErrorMessage(result.message)
            }
        })
    }

    let day = new Date().getDay();


    const handleNsdtAmount = async () => {
        await AuthService.getNsdtAmount().then(async result => {
            try {
                if (result?.msg === 'NSDT Price Fetched Successfully!!') {
                    // alertSuccessMessage(result.msg);
                    setNsdtWithdrawalPrice(result?.data?.nsdtWithdrawalPrice);

                }
                else {
                    alertErrorMessage(result.message);
                }
            } catch (error) {
                alertErrorMessage(error);
            }

        })
    }


    console.log(admintxHash, 'admintxHash');

    return (
            <div class="page-content">
                <div class="bg-dash-dark-2 py-4">
                    <div class="container-fluid">
                        <h2 class="h5 mb-0"> Withdrow Income</h2>
                    </div>
                </div>
                <section>
                    <div class="container-fluid">
                        <div class="row gy-4 justify-content-center">
                            <div class="col-md-6 col-sm-6">
                                <a href="javascript:void(0)" class="card card_hover mb-0">
                                    <div class="card-body">
                                        <div class="d-flex align-items-end justify-content-between mb-2">
                                            <div class="me-2">
                                                <span class="svg-icon svg-icon-sm svg-icon-heavy text-gray-600 mb-2">
                                                    <i class="fas fa-angle-up text-success"></i>
                                                </span>
                                                <p class="text-sm text-uppercase text-gray-600 lh-1 mb-0">Main Wallet</p>
                                            </div>
                                            <p class="text-xxl lh-1 mb-0 text-dash-color-1"> ${mainWallet}</p>
                                        </div>
                                    </div>
                                </a>
                            </div>

                        </div>
                        <div class="row gy-4 justify-content-center mt-1">
                            <div class="col-md-6 col-sm-6">
                                <a href="javascript:void(0)" class="card mb-0">
                                    <div class="card-body text-white">
                                        <p>Important Note:</p>
                                        <p >
                                            Minimum withdrawal is $ 5% of the total amount will be charge as withdrawal Transaction
                                            Charges...!
                                        </p>
                                        <p>
                                            10% will be dedected for Liquidity Pool (Land Acquisition Charge)
                                        </p>
                                        <p className=" mt-3" >
                                            Current Rate : 1 NSDT = $ {nsdtWithdrawalPrice}
                                        </p>
                                    </div>
                                </a>
                            </div>
                        </div>
                        <div class="row gy-4 mt-4">
                            <div class="col-lg-6 m-auto">
                                <div class="card  h-100">
                                    <div class="card-header d-flex align-items-end justify-content-between">
                                        <h3 class="h4 mb-0">OTP is send to your register email</h3>
                                        {!walletAddr ?
                                            <button type="button" className="btn btn-primary w-auto btn-sm" onClick={() => WalletConnect()}>Wallet Connect</button> :
                                            <button type="button" className="btn btn-success w-auto btn-sm">Connected</button>
                                        }
                                    </div>
                                    <div class="card-body pt-0">
                                        <form>
                                            <div class="mb-3">
                                                <label class="form-label" for="exampleInputOTP">Wallet type </label>
                                                <input class="form-control" id="exampleInputMember" type="text" aria-describedby="Member"
                                                    name="walletType" value={walletType} disabled />
                                            </div>
                                            <div class="mb-3">
                                                <label class="form-label" for="exampleInputMember">Wallet Address</label>
                                                <input class="form-control" id="exampleInputMember" type="text" aria-describedby="Member"
                                                    name="walletAddr" value={walletAddr} disabled />
                                            </div>
                                            <div class="mb-3">
                                                <label class="form-label" for="exampleInputName">Amount</label>
                                                <input class="form-control" id="exampleInputName" type="text" aria-describedby="Name"
                                                    name="amount" value={amount} onChange={handleInputChange} />
                                            </div>
                                            <div className="mb-3 d-flex  align-items-center justify-content-between" >
                                                {mainWallet < amount ? <span style={{ color: 'red' }}>Insufficient Balance</span> :

                                                    <button className="btn btn-primary" type="button" onClick={handleCalculateTbac} disabled={!amount}>
                                                        Calculate NSDT
                                                    </button>
                                                }
                                                <p>
                                                    NSDT Amount : <strong className="text-white" >{calculateAmount}</strong>
                                                </p>
                                            </div>
                                            <div class="mb-3">
                                                <label class="form-label" for="exampleInputEmail1">Enter OTP</label>
                                                <div className="input-group" >
                                                    <input class="form-control" id="exampleInputEmail1" type="text" aria-describedby="Email"
                                                        name="otp" value={otp} onChange={handleInputChange} />

                                                    <GetcodeButton className="btn btn-primary mb-0"
                                                        onClick={() => handleGetCode(loginid)}>
                                                        <span>Get Code</span>
                                                    </GetcodeButton>
                                                </div>
                                            </div>
                                            <div class="mb-3">
                                                <label class="form-label" for="exampleInputEmail1">Enter Transtion Password</label>
                                                <input class="form-control" id="exampleInputEmail1" type="password" aria-describedby="Email"
                                                    name="transtionPass" value={transtionPass} onChange={handleInputChange} />
                                            </div>

                                            <div class="mb-3">
                                                <label class="form-label" for="exampleInputEmail1"> Enter Transtion Hash:</label>
                                                <input class="form-control" type="text" value={transtionHash} name='transtionHash' disabled />
                                            </div>

                                            {
                                                amount >= 101 ? 'Maximum Withdral Limit is $100' :





                                                    withdrawalStatus === 'Inactive' || lastWithdrawal === 1 ?
                                                        <strong className="m-auto d-block text-center text-danger ">Withdrawal Is Disabled</strong>
                                                        :
                                                        <>
                                                            <button class="btn btn-secondary me-3" type="button" onClick={transferToAdmin}> Get Coins </button>
                                                            {/* <button class="btn btn-primary" type="button" onClick={() => handleWithdrowIncome(walletType, walletAddr, amount, otp, transtionPass, transtionHash, admintxHash)} disabled={!admintxHash}>Submit</button> */}
                                                        </>
                                            }
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>

                {
                    amount >= 101 ?

                        alertErrorMessage('Maximum Withdral Limit is $100')
                        : ''
                }

            </div>
    )
}

export default WithdrawPage;